}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --text-primery-color: #000;
  --text-secondary-color: #fff;
  --text-accent-color: #d41443;
  --text-lable-color: #ffa5ba;
  --text-about-color: #907e82;
  --text-contacts-color: #907e82;
  --text-btn-color: #fff;
  --text-btn-accent-color: #d41443;
  --text-footer-marks-color: #f0d1a5;
  --bg-primery-color: #fff;
  --linear-gradient: #fff5f6;
  --bg-product1: #ffb8ca;
  --bg-product2: #f0d1a5;
  --bg-product3: #c2e297;
  --bg-contacts1: #f0d1a5;
  --bg-contacts2: #c2e297;
  --bg-contacts3: #f0d1a5;
  --bg-btn-color: #fff;
  --bg-btn-accent-color: #d41443;
  --bg-comma-color: #e1e1e1;
  --bg-icon-accent-color: #fea5bb;
  --bg-icon-hover-color: #d41443;
  --bg-btn-hover: #ecebeb;
  --bg-menu-color: #e17992;
  --bg-customer-color: #fafafa;
  --bg-button-color: #d41342;
  --icon-color: #fff;
  --icon-accent-color: #ffa5ba;
  --border-line-color: #e1e1e1;
  --font-family-secondary: "Titan One", sans-serif;
  --transition: 250ms cubic-bezier(.4, 0, .2, 1);
  --color-customer-slider: #d9d9d9;
  --drop-shadow: 0px 4px 4px rgba(0, 0, 0, .25);
  --test: white;
}

body {
  color: var(--text-primery-color);
  background-color: var(--bg-primery-color);
  font-family: DM Sans, sans-serif;
  text-decoration: none;
}

textarea {
  resize: none;
  outline: none;
}

input {
  outline: none;
}

ul, p, li, h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
}

ul, a {
  text-decoration: none;
  list-style: none;
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*, :after, :before {
  box-sizing: inherit;
}

link {
  color: inherit;
  text-decoration: none;
}

.list {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

