}

.slider__fulltxt {
  z-index: 100;
  display: block;
  position: relative;
}

.slider__shorttxt {
  width: 114px;
  display: block;
}

.slider__shorttxt:after {
  content: "";
  width: 18px;
  height: 18px;
  background-image: url("arrow-right.71f33251.svg");
  background-size: contain;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  margin-top: 7px;
  display: flex;
}

.slider__fulltxt[open] {
  width: 100%;
  max-height: 150px;
  color: var(--text-about-color);
  background-image: linear-gradient(to top, rgba(255, 255, 255, .1), rgba(255, 255, 255, .9));
  border-radius: 5px;
  padding: 10px 10px 35px;
  overflow-y: auto;
  box-shadow: 0 8px 50px rgba(210, 20, 67, .15);
}

.slider__fulltxt[open] .slider__shorttxt {
  width: 100%;
}

.slider__fulltxt[open] .slider__shorttxt:after {
  content: "";
  display: block;
  position: absolute;
  bottom: 10px;
  left: 10px;
  transform: rotate(270deg);
}

@media screen and (min-width: 1200px) {
  .slider__img {
    max-width: 213px;
  }

  .slider__shorttxt {
    width: 200px;
  }

  .slider__shorttxt:after {
    width: 26px;
    height: 26px;
    margin-top: 20px;
  }

  .slider__fulltxt[open] {
    max-height: 230px;
    background-image: linear-gradient(to top right, rgba(255, 255, 255, .1), #fff);
    border-radius: 12px;
    padding: 15px 15px 50px;
  }

  .slider__fulltxt[open] .slider__shorttxt:after {
    bottom: 15px;
    left: 20px;
  }
}

